export default "import React, { useCallback, useEffect, useRef } from 'react'\nimport {\n  EmblaCarouselType,\n  EmblaEventListType,\n  EmblaEventModelType,\n  EmblaOptionsType\n} from 'embla-carousel'\nimport useEmblaCarousel from 'embla-carousel-react'\nimport {\n  NextButton,\n  PrevButton,\n  usePrevNextButtons\n} from '../../EmblaCarouselArrowButtons'\nimport { DotButton, useDotButton } from '../../EmblaCarouselDotButton'\n\nconst TWEEN_FACTOR_BASE = 0.52\n\nconst numberWithinRange = (number: number, min: number, max: number): number =>\n  Math.min(Math.max(number, min), max)\n\ntype PropType = {\n  slides: number[]\n  options?: EmblaOptionsType\n}\n\nconst EmblaCarousel = (props: PropType) => {\n  const { slides, options } = props\n  const [emblaRef, emblaApi] = useEmblaCarousel(options)\n  const tweenFactor = useRef(0)\n  const tweenNodes = useRef<HTMLElement[]>([])\n\n  const { selectedIndex, scrollSnaps, onDotButtonClick } =\n    useDotButton(emblaApi)\n\n  const {\n    prevBtnDisabled,\n    nextBtnDisabled,\n    onPrevButtonClick,\n    onNextButtonClick\n  } = usePrevNextButtons(emblaApi)\n\n  const setTweenNodes = useCallback((emblaApi: EmblaCarouselType): void => {\n    tweenNodes.current = emblaApi.slideNodes().map((slideNode) => {\n      return slideNode.querySelector('.embla__slide__number') as HTMLElement\n    })\n  }, [])\n\n  const setTweenFactor = useCallback((emblaApi: EmblaCarouselType) => {\n    tweenFactor.current = TWEEN_FACTOR_BASE * emblaApi.snapList().length\n  }, [])\n\n  const tweenScale = useCallback(\n    <EventType extends keyof EmblaEventListType>(\n      emblaApi: EmblaCarouselType,\n      event?: EmblaEventModelType<EventType>\n    ) => {\n      const engine = emblaApi.internalEngine()\n      const scrollProgress = emblaApi.scrollProgress()\n      const slidesInView = emblaApi.slidesInView()\n      const isScrollEvent = event?.type === 'scroll'\n\n      emblaApi.snapList().forEach((scrollSnap, snapIndex) => {\n        let diffToTarget = scrollSnap - scrollProgress\n        const slidesInSnap = engine.scrollSnapList.slidesBySnap[snapIndex]\n\n        slidesInSnap.forEach((slideIndex) => {\n          if (isScrollEvent && !slidesInView.includes(slideIndex)) return\n\n          if (engine.options.loop) {\n            engine.slideLooper.loopPoints.forEach((loopItem) => {\n              const target = loopItem.target()\n\n              if (slideIndex === loopItem.index && target !== 0) {\n                const sign = Math.sign(target)\n\n                if (sign === -1) {\n                  diffToTarget = scrollSnap - (1 + scrollProgress)\n                }\n                if (sign === 1) {\n                  diffToTarget = scrollSnap + (1 - scrollProgress)\n                }\n              }\n            })\n          }\n\n          const tweenValue = 1 - Math.abs(diffToTarget * tweenFactor.current)\n          const scale = numberWithinRange(tweenValue, 0, 1).toString()\n          const tweenNode = tweenNodes.current[slideIndex]\n          tweenNode.style.transform = `scale(${scale})`\n        })\n      })\n    },\n    []\n  )\n\n  useEffect(() => {\n    if (!emblaApi) return\n\n    setTweenNodes(emblaApi)\n    setTweenFactor(emblaApi)\n    tweenScale(emblaApi)\n\n    emblaApi\n      .on('reinit', setTweenNodes)\n      .on('reinit', setTweenFactor)\n      .on('reinit', tweenScale)\n      .on('scroll', tweenScale)\n      .on('slidefocus', tweenScale)\n  }, [emblaApi, tweenScale])\n\n  return (\n    <div className=\"embla\">\n      <div className=\"embla__viewport\" ref={emblaRef}>\n        <div className=\"embla__container\">\n          {slides.map((index) => (\n            <div className=\"embla__slide\" key={index}>\n              <div className=\"embla__slide__number\">{index + 1}</div>\n            </div>\n          ))}\n        </div>\n      </div>\n\n      <div className=\"embla__controls\">\n        <div className=\"embla__buttons\">\n          <PrevButton onClick={onPrevButtonClick} disabled={prevBtnDisabled} />\n          <NextButton onClick={onNextButtonClick} disabled={nextBtnDisabled} />\n        </div>\n\n        <div className=\"embla__dots\">\n          {scrollSnaps.map((_, index) => (\n            <DotButton\n              key={index}\n              onClick={() => onDotButtonClick(index)}\n              className={'embla__dot'.concat(\n                index === selectedIndex ? ' embla__dot--selected' : ''\n              )}\n            />\n          ))}\n        </div>\n      </div>\n    </div>\n  )\n}\n\nexport default EmblaCarousel\n";